import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import './header.scss'

import Logo from './Logo/Logo'
import Menu from './Menu/Menu'

class Header extends Component {

    state = {
        isSwitchOnSearch: false,
        isSwitchOnBurger: false,
    }

    onSearchClick () {
        this.setState ((prevState) => ({
            isSwitchOnSearch: !this.state.isSwitchOnSearch
        }))
    }
    renderSearchButton () {
        return (
            <div className={this.state.isSwitchOnSearch ? "header-search_btn header-search_btn_toggle" : "header-search_btn"} onClick =   {() => this.onSearchClick()}>
                <FontAwesomeIcon icon = {this.state.isSwitchOnSearch ? " " : "search" } />
                <span></span>
            </div>
        )
    }
    renderSearch () {
        return (
            <div className={this.state.isSwitchOnSearch ? "block-search block-search_open" : "block-search"}>
                <div className="container">
                    <div className="row">
                        <form action="">
                            <div className="col-xs-10">
                                <input type="text" className="block-search_input" placeholder="Поиск"/>
                            </div>
                            <div className="col-xs-2">
                                <button className="block-search_btn">
                                    <FontAwesomeIcon icon = {['fas', 'search']} />
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }

    onBurgerClick () {
        this.setState ((prevState) => ({
            isSwitchOnBurger: !this.state.isSwitchOnBurger
        }))
    }
    renderBurgerButton() {
        return (
            <div className={this.state.isSwitchOnBurger ? "header_burger header_burger_toggle" : "header_burger"} onClick = {()     => this.onBurgerClick()}>
                <span></span>
            </div>
        )
    }
    renderMenu() {
        return (
            <div className={this.state.isSwitchOnBurger ? "menu menu_toggle" : "menu"}>
                <div className="container">
                    <div className="row">
                        <div className="col-xs-12 col-sm-12 col-md-12">
                            <Menu/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    render () {
        return (
            <header>
                <section className="header">
                    <div className="container">
                        <div className="row">
                            <div className="col-xs-8 col-sm-10">
                                <Logo/>
                            </div>
                            <div className="col-xs-2 col-md-1 col-sm-1">
                                {this.renderSearchButton()}
                            </div>
                            <div className="col-xs-2 col-sm-1">
                                {this.renderBurgerButton()}
                            </div>
                        </div>
                    </div>
                </section>
                {this.renderMenu()}
                {this.renderSearch()}
            </header>
        )
    }
}
    

export default Header