import React, { Component } from 'react'
import { Route } from 'react-router-dom'

import './main.scss'

import News from './News/News'
import MainNews from './News/MainNews'
import newsDataBase from './News/newsDataBase'
import NewsPages from '../../features/News/NewsPages'
import ReviewsPage from './ReviewsPage/ReviewsPage'
import NewsPage from './NewsPage/NewsPage'
import ArticlesPage from './ArticlesPage/ArticlesPage';

class Main extends Component {
    state = {
        mainNews: {
            mainNews: 7,
        },
    }
    render () {
        return (
            <main className="main">
                <div>
                    <Route exact path="/" render={() => (
                        <section className="main-news_section" id="news">
                            <div className="container">
                                <div className="row">
                                    {
                                        newsDataBase.map(({
                                            id,
                                            date,
                                            category,
                                            title,
                                            image,
                                            description,
                                            tags,
                                        }) => (
                                            <div key = {id} >{   
                                                id === this.state.mainNews[tags]
                                            ?
                                                <div>
                                                    <Route exact path = "/" render = {() => (
                                                        <MainNews
                                                            id = {id}
                                                            date = {date}
                                                            category = {category}
                                                            title = {title}
                                                            image = {image}
                                                            tags = {tags}
                                                        />
                                                    )} />
                                                </div>
                                                : null
                                                }
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </section>
                    )}/>
                    <section className="news_section">
                        <div className="container">
                            <div className="row">
                                {
                                    newsDataBase.map(({
                                        id,
                                        date,
                                        category,
                                        title,
                                        image,
                                        description,
                                        tags,
                                    }) => (
                                        <div key = {id}>
                                            <Route exact path = "/" render = {() => (
                                                <News
                                                    id = {id}
                                                    date = {date}
                                                    category = {category}
                                                    title = {title}
                                                    image = {image}
                                                />
                                            )} />
                                            <Route exact path = "/news" render = {() => (
                                                <NewsPage
                                                    id = {id}
                                                    date = {date}
                                                    category = {category}
                                                    title = {title}
                                                    image = {image}
                                                />
                                            )} />
                                            <Route exact path = "/reviews" render = {() => (
                                                <ReviewsPage
                                                    id = {id}
                                                    date = {date}
                                                    category = {category}
                                                    title = {title}
                                                    image = {image}
                                                />
                                            )} />
                                            <Route exact path = "/articles" render = {() => (
                                                <ArticlesPage
                                                    id = {id}
                                                    date = {date}
                                                    category = {category}
                                                    title = {title}
                                                    image = {image}
                                                />
                                            )} />
                                        </div> 
                                    ))
                                }
                                <Route path = "/:newsCategory/:newsTitle" component = {NewsPages}/> 
                            </div>
                        </div>
                    </section> 
                </div>
            </main>
        )
    }
}

export default Main