        id: 6,
        date: "13.02.2019",
        category: "Новости",
        title: "Apple сняла рекламу FaceID в стиле японцев",
        image: "image/iphone-x-face-id1.jpg",
        description: "<p>Недавно Apple сняла новый рекламный ролик iPhone X, где продемонстрировала FaceID — что это такое и как этим пользоваться. Сделали это абсолютно сумасшедшим способом. <br><br> В ролике мужчина сидит в каком-то концертном зале и участвует в шоу. Его спрашивают о пароле к банковскому счету, но он его никак не может назвать. Участник шоу изо-всех сил пытается это сделать, но ничего не выходит, но потом человек вспоминает, что вместо пароля можно использовать лицо. <br><br> <iframe width='100%' height='480' src='https://www.youtube.com/embed/vcsGu9ug9J4' frameborder='0' allow='accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture' allowfullscreen></iframe> <br><br> В конце рекламы говорится, что не обязательно держать все пароли в голове: <br><br> <blockquote>Если вам нужно запомнить пароль, то необязательно напрягать свою память. С FaceID на iPhone X ваше лицо становится паролем.</blockquote></p>",
        tags: "mainNews",
