import React from 'react'
import './NewsPages.scss'
import newsDataBase, { getNewsMap } from './../../App/Main/News/newsDataBase'

const NewsPage = ({
    newsMap = getNewsMap(newsDataBase),
    match,
}) => {
    return (
        console.log(match),
        <div className = "col-xs-12 newsPage" key = {newsMap[match.params.newsTitle].id}>
            <div className = "col-xs-12 col-sm-8 col-sm-offset-2">
                <h1 className = "newsPage-title">{newsMap[match.params.newsTitle].title}</h1>
                <p className = "newsPage-category">{newsMap[match.params.newsTitle].category}</p>
                <p className = "newsPage-date">{newsMap[match.params.newsTitle].date}</p>
            </div>
            <img src = {newsMap[match.params.newsTitle].image} alt={newsMap[match.params.newsTitle].title} className = "newsPage-img"/>
            <div className = "col-xs-12 col-sm-8 col-sm-offset-2">
                <p className = "newsPage-description" dangerouslySetInnerHTML= {{
                    __html: newsMap[match.params.newsTitle].description,
                }}></p>
            </div>
        </div>
    )
}

export default NewsPage