import React from 'react'
import {Link} from 'react-router-dom'

const Menu = () =>
<div className="footer-menu">
	<Link to="/news" className="footer-menu_links">Новости</Link>
	<Link to="/reviews" className="footer-menu_links">Обзоры</Link>
	<Link to="/articles" className="footer-menu_links">Статьи</Link>
</div>

export default Menu