import React, { Component } from 'react'
import {Link} from 'react-router-dom'

class MainNews extends Component {
    render () {
        const {
            id,
            category,
            date,
            title,
            image,
        } = this.props
        const figureStyle = {
            backgroundImage: 'url(' + image + ')',
        }
        return (
            <div className="col-xs-12" key = {id}>
                <div className="main-news">
                    <Link to = {`/${category}/${title}`} className="main-news_link">
                        <div className="main-news-card">
                            <figure className="main-news-card_img" style = {figureStyle}></figure>
                            <h2 className="main-news-card_title">
                                <div className="card-info">
                                    <p className="card-info_category">
                                        {category}
                                    </p>
                                    <p className="card-info_date">{date}</p>
                                </div>
                                {title}
                            </h2>
                        </div>
                    </Link>
                </div>
            </div>
        )
    }
}

export default MainNews