import React from 'react'
import {Link} from 'react-router-dom'

import logo from './logo.svg'

const Logo = () =>
<Link to = "/" className = "header_logo">
    <img src = {logo} alt = "АйNews" className = "header_logo-img"/>
    <span className = "header_logo-span">Ай</span>News
</Link>

export default Logo