import React, {Component} from 'react'
import {Link} from 'react-router-dom'

class News extends Component {
    render () {
        const {
            id,
            date,
            category,
            title,
            image,
        } = this.props
        const figureStyle = {
            backgroundImage: 'url(' + image + ')',
        }
        return (
            <div className="col-xs-12 col-sm-6" key = {id}>
                <div className="news">
                    <Link to = {`/${category}/${title}`} className="news_link">
                        <div className="news-card">
                            <figure className="news-card_img" style={figureStyle}></figure>
                                <h2 className="news-card_title">
                                    <div className="card-info">
                                        <p className="card-info_category">{category}</p>
                                        <p className="card-info_date">{date}</p>
                                    </div>
                                    {title}
                                </h2>
                        </div>
                    </Link>
                </div>
            </div>
        )
    }
}

export default News