import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Social = () =>
    <div className="footer-social">
			<a href="https://youtube.com/" target="blank" className="footer-social_links">
				<FontAwesomeIcon icon = {['fab', 'youtube']}/>
			</a>
			<a href="https://instagram.com/" target="blank" className="footer-social_links">
				<i className="fab fa-instagram"></i>
				<FontAwesomeIcon icon = {['fab', 'instagram']} />
			</a>
			<a href="https://facebook.ru/" target="blank" className="footer-social_links">
				<FontAwesomeIcon icon = {['fab', 'facebook-f']} />
			</a>
			<a href="https://vk.com/" target="blank" className="footer-social_links">
				<FontAwesomeIcon icon = {['fab', 'vk']} />
			</a>
	</div>

export default Social