import React from 'react'
import {Link} from 'react-router-dom'

const Menu = () =>
    <nav className="header_menu">
        <Link to = "/news" className="header_menu-link">Новости</Link>
        <Link to = "/reviews" className="header_menu-link">Обзоры</Link>
        <Link to = "/articles" className="header_menu-link">Статьи</Link>
    </nav>

export default Menu